import { useEffect, useRef, useState } from 'react';
import { MessageList } from '../MessageList';
import { MessageInput } from '../MessageInput';
import { CompanyLogo } from '../CompanyLogo';
import { useChatStore } from '../../store/chatStore';
import { useTheme } from '../../hooks/useTheme';
import { A2AClient } from '../../a2aclient/A2AClient';
import type { Message as A2AMessage, MessagePart, Part } from '../../a2aclient/types';
import styles from './ChatWindow.module.css';
import type { ChatWidgetProps, Message, Attachment } from '../../types';

interface ChatWindowPropsWithA2A extends ChatWidgetProps {
  agentUrl?: string; // Optional A2A agent URL
}

export function ChatWindow(props: ChatWindowPropsWithA2A) {
  const { 
    serverUrl,
    theme,
    onMessage,
    onConnectionChange,
    userId,
    metadata,
    placeholder,
    welcomeMessage,
    allowFileUpload,
    maxFileSize,
    allowedFileTypes,
    agentUrl
  } = props;
  
  const chatTheme = useTheme(theme);
  const { addMessage, setConnected, isConnected, updateMessage, setTyping } = useChatStore();
  const a2aClientRef = useRef<A2AClient | null>(null);
  const [supportsSSE, setSupportsSSE] = useState(false);
  const [agentName, setAgentName] = useState<string>('Agent');
  const wsRef = useRef<WebSocket | null>(null);
  const currentTaskIdRef = useRef<string | undefined>();
  const currentContextIdRef = useRef<string | undefined>();
  const streamActiveRef = useRef<boolean>(false);
  
  useEffect(() => {
    // Initialize A2A client if agent URL is provided
    if (agentUrl) {
      const client = new A2AClient(agentUrl, { debug: true });
      a2aClientRef.current = client;
      
      // Check agent capabilities
      client.getAgentCard()
        .then(async (card) => {
          setConnected(true);
          onConnectionChange?.(true);
          
          // Set agent name from card
          if (card.name) {
            setAgentName(card.name);
          }
          
          // Check if agent supports SSE
          const supportsStreaming = await client.supportsStreaming();
          setSupportsSSE(supportsStreaming);
          // Agent supports SSE
        })
        .catch((error) => {
          console.error('Failed to connect to A2A agent:', error);
          setConnected(false);
          onConnectionChange?.(false);
        });
      
      return () => {
        // Cleanup A2A client if needed
      };
    }
    
    // Otherwise use WebSocket connection
    let reconnectTimeout: number | null = null;
    
    const connect = () => {
      try {
        const ws = new WebSocket(serverUrl);
        wsRef.current = ws;
        
        ws.onopen = () => {
          setConnected(true);
          onConnectionChange?.(true);
          
          // Send initial metadata
          if (userId || metadata) {
            ws.send(JSON.stringify({
              type: 'init',
              userId,
              metadata
            }));
          }
        };
        
        ws.onclose = () => {
          setConnected(false);
          onConnectionChange?.(false);
          
          // Attempt to reconnect after 3 seconds
          reconnectTimeout = window.setTimeout(connect, 3000);
        };
        
        ws.onerror = () => {
          setConnected(false);
          onConnectionChange?.(false);
        };
        
        ws.onmessage = (event) => {
          try {
            const data = JSON.parse(event.data);
            
            if (data.type === 'message') {
              const message: Message = {
                id: data.id || generateId(),
                content: data.content,
                sender: data.sender || 'assistant',
                timestamp: new Date(data.timestamp || Date.now()),
                status: 'sent',
                metadata: data.metadata,
                attachments: data.attachments
              };
              
              addMessage(message);
              onMessage?.(message);
              setTyping(false);
            } else if (data.type === 'typing') {
              setTyping(data.isTyping || false);
            }
          } catch (error) {
            console.error('Failed to parse message:', error);
          }
        };
      } catch (error) {
        console.error('Failed to connect:', error);
        setConnected(false);
        onConnectionChange?.(false);
      }
    };
    
    if (!agentUrl) {
      connect();
    }
    
    return () => {
      if (reconnectTimeout) {
        clearTimeout(reconnectTimeout);
      }
      if (wsRef.current) {
        wsRef.current.close();
      }
    };
  }, [serverUrl, agentUrl, userId, metadata, setConnected, onConnectionChange, addMessage, onMessage, setTyping]);

  const formatPart = (part: Part): string => {
    if (part.kind === 'text') {
      return part.text;
    } else if (part.kind === 'file') {
      return `[File: ${part.file.name || 'Unnamed'}]`;
    } else if (part.kind === 'data') {
      return `[Data: ${JSON.stringify(part.data)}]`;
    }
    return '[Unknown part type]';
  };
  
  const handleSendMessage = async (content: string, attachments?: Attachment[]) => {
    const message: Message = {
      id: generateId(),
      content,
      sender: 'user',
      timestamp: new Date(),
      status: 'sending',
      attachments
    };
    
    addMessage(message);
    
    if (agentUrl && a2aClientRef.current) {
      // Use A2A client to send message
      try {
        const a2aMessage: A2AMessage = {
          kind: 'message',
          messageId: message.id,
          role: 'user',
          parts: [{ kind: 'text', text: content }] as MessagePart[]
        };

        // Add task and context IDs if we have them
        if (currentTaskIdRef.current) {
          a2aMessage.taskId = currentTaskIdRef.current;
        }
        if (currentContextIdRef.current) {
          a2aMessage.contextId = currentContextIdRef.current;
        }

        // Add attachments if present
        if (attachments && attachments.length > 0) {
          // TODO: Handle file attachments with A2A protocol
          console.warn('File attachments not yet implemented for A2A protocol');
        }

        // Sending message via A2A

        if (supportsSSE) {
          // Use streaming for real-time responses
          try {
            const stream = a2aClientRef.current.sendMessageStream({
              message: a2aMessage,
              configuration: {
                acceptedOutputModes: ['text']
              }
            });

            const artifactMessages: Message[] = [];
            streamActiveRef.current = true;
            setTyping(true);
            
            for await (const event of stream) {
              
              // Handle status-update events (this is where the agent's messages come from)
              if ((event as any).kind === 'status-update') {
                const update = event as any;
                
                if (update.status?.message) {
                  // Extract message content from parts
                  const content = update.status.message.parts.map(formatPart).join('\n');
                  
                  // Only add non-empty messages or final messages
                  if (content.trim() || update.final) {
                    const assistantMessage: Message = {
                      id: generateId(),
                      content,
                      sender: 'assistant',
                      timestamp: new Date(),
                      status: 'sent'
                    };
                    
                    // Add assistant message from status update
                    addMessage(assistantMessage);
                    onMessage?.(assistantMessage);
                  }
                }

                // Update task and context IDs
                if (update.taskId) {
                  currentTaskIdRef.current = update.taskId;
                }
                if (update.contextId) {
                  currentContextIdRef.current = update.contextId;
                }

                // Clear task ID if final and not input-required
                if (update.final && update.status?.state !== 'input-required') {
                  currentTaskIdRef.current = undefined;
                  streamActiveRef.current = false;
                  setTyping(false);
                }
              }
              
              // Handle artifact-update events (code generation, file content, etc.)
              else if ((event as any).kind === 'artifact-update') {
                const update = event as any;
                if (update.artifact && update.artifact.parts) {
                  const artifactName = update.artifact.name || 'Artifact';
                  const content = update.artifact.parts.map(formatPart).join('\n');
                  
                  if (content.trim()) {
                    // Format as a code block if it looks like code
                    const formattedContent = artifactName.endsWith('.cs') || artifactName.endsWith('.js') || artifactName.endsWith('.ts') || artifactName.endsWith('.py') || artifactName.endsWith('.java') || artifactName.endsWith('.cpp') || artifactName.endsWith('.c') || artifactName.endsWith('.h') ?
                      `\`\`\`${getLanguageFromFilename(artifactName)}\n${content}\n\`\`\`` :
                      content;
                    
                    const artifactMessage: Message = {
                      id: generateId(),
                      content: `**${artifactName}**\n\n${formattedContent}`,
                      sender: 'assistant',
                      timestamp: new Date(),
                      status: 'sent',
                      metadata: { isArtifact: true, artifactName }
                    };
                    
                    // Store artifact messages to add them before the final status message
                    artifactMessages.push(artifactMessage);
                  }
                }
              }
              
              // Handle direct message events
              else if ((event as any).kind === 'message' && (event as any).role === 'agent') {
                const msg = event as A2AMessage;
                const content = msg.parts.map(formatPart).join('\n');
                
                const assistantMessage: Message = {
                  id: generateId(),
                  content,
                  sender: 'assistant',
                  timestamp: new Date(),
                  status: 'sent'
                };
                
                // Add assistant message from message event
                addMessage(assistantMessage);
                onMessage?.(assistantMessage);
                
                // Update IDs
                if (msg.taskId) {
                  currentTaskIdRef.current = msg.taskId;
                }
                if (msg.contextId) {
                  currentContextIdRef.current = msg.contextId;
                }
              }
              
              // Handle task events
              else if ((event as any).kind === 'task') {
                const task = event as any;
                if (task.id) {
                  currentTaskIdRef.current = task.id;
                }
                if (task.contextId) {
                  currentContextIdRef.current = task.contextId;
                }
              }
            }
            // Add any artifact messages that were collected
            for (const artifactMsg of artifactMessages) {
              addMessage(artifactMsg);
              onMessage?.(artifactMsg);
            }
            
            // Stream ended
            streamActiveRef.current = false;
            setTyping(false);
          } catch (streamError) {
            console.error('Error in stream processing:', streamError);
            streamActiveRef.current = false;
            setTyping(false);
          }
        } else {
          // Use simple request/response
          // Using simple request/response
          const response = await a2aClientRef.current.sendMessage({
            message: a2aMessage,
            configuration: {
              acceptedOutputModes: ['text']
            }
          });

          if ('result' in response && response.result) {
            // Handle the response based on its structure
            let agentMessage: A2AMessage | undefined;
            
            if (response.result.kind === 'message') {
              agentMessage = response.result;
            }
            
            if (agentMessage && agentMessage.role === 'agent') {
              const content = agentMessage.parts.map(formatPart).join('\n');
              
              const assistantMessage: Message = {
                id: agentMessage.messageId || generateId(),
                content,
                sender: 'assistant',
                timestamp: new Date(),
                status: 'sent'
              };
              
              // Add assistant message
              addMessage(assistantMessage);
              onMessage?.(assistantMessage);
              
              // Update IDs
              if (agentMessage.taskId) {
                currentTaskIdRef.current = agentMessage.taskId;
              }
              if (agentMessage.contextId) {
                currentContextIdRef.current = agentMessage.contextId;
              }
            }
          }
        }

        // Update original message status to sent
        updateMessage(message.id, { status: 'sent' });
        
      } catch (error) {
        console.error('Error sending message to A2A agent:', error);
        updateMessage(message.id, { status: 'error' });
      }
    } else if (isConnected && wsRef.current) {
      // Send via WebSocket if connected
      const messageData = {
        type: 'message',
        id: message.id,
        content,
        userId,
        metadata: {
          ...metadata,
          ...message.metadata
        },
        attachments
      };
      
      wsRef.current.send(JSON.stringify(messageData));
      updateMessage(message.id, { status: 'sent' });
      onMessage?.(message);
      setTyping(true);
    } else {
      console.error('No connection available');
      updateMessage(message.id, { status: 'error' });
    }
  };
  
  const showHeaderLogo = chatTheme.branding?.logoPosition === 'header';
  const showFooterLogo = chatTheme.branding?.logoPosition === 'footer';
  
  return (
    <div className={`${styles.chatWindow} chat-widget-container`}>
      {showHeaderLogo && (
        <div className={styles.header}>
          <CompanyLogo />
        </div>
      )}
      
      <MessageList welcomeMessage={welcomeMessage} agentName={agentName} />
      
      {showFooterLogo && (
        <div className={styles.footer}>
          <CompanyLogo />
        </div>
      )}
      
      <MessageInput
        onSendMessage={handleSendMessage}
        placeholder={placeholder}
        allowFileUpload={allowFileUpload}
        maxFileSize={maxFileSize}
        allowedFileTypes={allowedFileTypes}
      />
    </div>
  );
}

function generateId(): string {
  return `${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
}

function getLanguageFromFilename(filename: string): string {
  const ext = filename.split('.').pop()?.toLowerCase();
  const languageMap: Record<string, string> = {
    'js': 'javascript',
    'ts': 'typescript',
    'tsx': 'typescript',
    'jsx': 'javascript',
    'py': 'python',
    'java': 'java',
    'cs': 'csharp',
    'cpp': 'cpp',
    'c': 'c',
    'h': 'c',
    'hpp': 'cpp',
    'rb': 'ruby',
    'go': 'go',
    'rs': 'rust',
    'php': 'php',
    'swift': 'swift',
    'kt': 'kotlin',
    'scala': 'scala',
    'r': 'r',
    'sql': 'sql',
    'sh': 'bash',
    'bash': 'bash',
    'ps1': 'powershell',
    'yaml': 'yaml',
    'yml': 'yaml',
    'json': 'json',
    'xml': 'xml',
    'html': 'html',
    'css': 'css',
    'scss': 'scss',
    'sass': 'sass',
    'less': 'less',
    'md': 'markdown'
  };
  return languageMap[ext || ''] || '';
}